import React, { Component } from "react";

import { Container, Row, Col, Card, Image } from "react-bootstrap";

import { Fade } from "react-reveal";

import SkillData from "./data/SkillData";

import valve_index from "../img/valveindex.png";

// https://codepen.io/drehimself/pen/QNXpyp

export class Me extends Component {
  render() {
    const darkMode = this.props.dm;
    const textColor = this.props.dm ? "text-light" : "text-dark";
    const bgColor = this.props.dm ? "bg-dark" : "bg-light";
    console.log(SkillData);

    const array = () => {
      var item_num = 2;
      var returnArray = [];
      for (let index = 0; index < item_num; index++) {
        returnArray.push(index);
      }
      return returnArray;
    };

    return (
      <Container
        className="h-100 d-flex align-items-center justify-content-center"
        style={{ paddingTop: 100 }}
        fluid="xl"
      >
        <div className="flex-direction-row">
          {array().map((item) => (
            <SkillCard />
          ))}
        </div>
      </Container>
    );
  }
}

function SkillCard(props) {
  return (
    <div className="bg-light mr-5 skill-card">
      {/* Image Div */}
      <div
        className="w-100 bg-warning d-flex justify-content-center align-items-center"
        style={{ height: "28vh", borderRadius: "25px 25px 0px 0px" }}
      >
        <Image src={valve_index} style={{zIndex: 99, height: "25vh"}} />
      </div>
    </div>
  );
}

export default Me;
